import React, { useContext, useMemo, useState } from 'react';
import { AppContext } from '@components/common/context/AppProvider';
import { getWeeklyBoss } from '@parsers/world-2/weeklyBosses';
import { Stack, Typography } from '@mui/material';
import Tabber from '@components/common/Tabber';
import WeeklyBoss from '@components/account/Worlds/World2/WeeklyBoss';
import { cleanUnderscore } from '@utility/helpers';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import Button from '@mui/material/Button';
import { format, isValid } from 'date-fns';
const WeeklyBosses = () => {
  const {
    state
  } = useContext(AppContext);
  const [weeks, setWeeks] = useState(10);
  const handleCopy = async data => {
    try {
      await navigator.clipboard.writeText(JSON.stringify(data, null, 2));
    } catch (err) {
      console.error(err);
    }
  };
  const weeklyBosses = useMemo(() => getWeeklyBoss(state?.account, weeks), [state?.account, weeks]);
  return weeklyBosses?.length ? <>
    <Stack direction={'row'} gap={2} mb={3} alignItems={'center'} data-sentry-element="Stack" data-sentry-source-file="weekly-bosses.jsx">
      <Button onClick={() => handleCopy(weeklyBosses)} data-sentry-element="Button" data-sentry-source-file="weekly-bosses.jsx"><ContentCopyIcon sx={{
          mr: 1
        }} data-sentry-element="ContentCopyIcon" data-sentry-source-file="weekly-bosses.jsx" /> Copy boss data</Button>
      <Button onClick={() => setWeeks(tempWeeks => tempWeeks + 10)} data-sentry-element="Button" data-sentry-source-file="weekly-bosses.jsx">+ Add more bosses</Button>
    </Stack>
    <Tabber forceScroll={weeks > 10} tabs={weeklyBosses?.map(({
      bossName,
      date
    }, index) => cleanUnderscore(bossName))} components={weeklyBosses?.map(({
      bossName,
      date
    }, index) => <Stack key={'boss-' + index}>
              <Typography variant={'body1'} sx={{
        textTransform: 'none'
      }}>{cleanUnderscore(bossName)}</Typography>
              <Typography variant={'caption'} sx={{
        textTransform: 'none'
      }}>{isValid(date) ? format(date, 'dd/MM/yyyy HH:mm:ss') : null}</Typography>
            </Stack>)} data-sentry-element="Tabber" data-sentry-source-file="weekly-bosses.jsx">
      {weeklyBosses?.map((boss, bossIndex) => <WeeklyBoss key={boss?.bossName + bossIndex} {...boss} bossIndex={bossIndex} account={state?.account} characters={state?.characters} />)}
    </Tabber>
  </> : <div data-sentry-component="WeeklyBosses" data-sentry-source-file="weekly-bosses.jsx">There is no random event data</div>;
};
export default WeeklyBosses;